@import url(https://fonts.googleapis.com/css2?family=Cinzel+Decorative&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Barlow&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Barlow&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.recipe {
  background-image: linear-gradient(-225deg, #e3fdf5 0%, #ffe6fa 100%);
  margin: 25px 15px;
  border-radius: 5%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  box-shadow: 0px 5px 10px rgb(68, 65, 65);
  border-radius: 10px;
  min-width: 30%;
  padding: 10px;
  font-family: "Barlow", "Cinzel Decorative";
}
h1 {
  font-family: "Cinzel Decorative";
  font-size: x-large;
}

ol {
  font-size: small;
}

.image {
  height: 100px;
  width: 100px;
  border-radius: 50%;
  padding: 5px;
  overflow: hidden;
  transition: all 0.5s ease-in-out;
}

.image:hover {
  transform: scale(1.1);
  border-radius: 10%;
}

.Link {
  display: flex;
  background-color: lightcoral;
  color: black;
  border-radius: 3px;
  margin-top: 8px;
  margin-bottom: 10px;
  cursor: pointer;
  box-shadow: 0px 2px 2px rgb(100, 96, 96);
}

.Link:hover {
  background-color: tomato;
  box-shadow: 0px 10px 10px rgb(100, 96, 96);
  -webkit-animation: rotate 0.7s ease-in-out both;
          animation: rotate 0.7s ease-in-out both;
}

.icon-link {
  margin-left: 0.75rem;
  font-size: 1.2rem;
}

a {
  margin: 0 auto;
  color: black;
  font-weight: bolder;
  font-family: "Courier New", Courier, monospace;
  text-decoration: none;
}

@-webkit-keyframes rotate {
  0% {
    transform: rotate(0deg) translate3d(0, 0, 0);
  }
  25% {
    transform: rotate(3deg) translate3d(0, 0, 0);
  }
  50% {
    transform: rotate(-3deg) translate3d(0, 0, 0);
  }
  75% {
    transform: rotate(1deg) translate3d(0, 0, 0);
  }
  100% {
    transform: rotate(0deg) translate3d(0, 0, 0);
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0deg) translate3d(0, 0, 0);
  }
  25% {
    transform: rotate(3deg) translate3d(0, 0, 0);
  }
  50% {
    transform: rotate(-3deg) translate3d(0, 0, 0);
  }
  75% {
    transform: rotate(1deg) translate3d(0, 0, 0);
  }
  100% {
    transform: rotate(0deg) translate3d(0, 0, 0);
  }
}

.App {
  min-height: 100vh;
  background-color: lightsteelblue;
}

.title-background {
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
    url("https://cdn.pixabay.com/photo/2017/06/06/22/46/mediterranean-cuisine-2378758_1280.jpg");
  background-size: cover;
  border-bottom: 20px solid thistle;
}

.shopName {
  margin-top: 0px;
  text-align: center;
  letter-spacing: 3px;

  color: white;
  padding: 15px;
  font-family: "Pacifico", "Roboto", "Courier New", Courier, monospace;
}

.image-title {
  height: 80px;
  width: 80px;
}

.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar {
  background-color: rgb(247, 247, 248);
  border: 0;
  width: 50%;
  height: 30px;
  padding: 0 20px;
  margin: 20px;
  border-radius: 10px;
  font-weight: bolder;
}

.search-bar::-webkit-input-placeholder {
  color: rgba(153, 141, 141, 0.5);
}

.search-bar:-ms-input-placeholder {
  color: rgba(153, 141, 141, 0.5);
}

.search-bar::-ms-input-placeholder {
  color: rgba(153, 141, 141, 0.5);
}

.search-bar::placeholder {
  color: rgba(153, 141, 141, 0.5);
}

.search-bar:focus,
.search-button:focus {
  outline: none;
}

.search-button {
  height: 30px;
  width: 85px;
  border-radius: 5px;
  border: 0;
  background-color: darkorange;
  cursor: pointer;
  font-weight: bolder;
  font-size: 15px;
  transition: all 0.2s linear;
}

.search-button:hover {
  transform: scaleY(1.1);
  background-color: rgb(253, 174, 4);

  box-shadow: 0px 5px 10px rgb(68, 65, 65);
}

.icon-search {
  margin-left: 0.1rem;
  font-size: 1.25rem;
}

.message-search {
  text-align: center;
  text-transform: capitalize;
  color: white;
  background-color: rgba(62, 145, 106, 0.5);
  margin-left: 20%;
  margin-right: 18%;
  margin-top: 4%;
  margin-bottom: 4%;
  font-family: "Barlow";
}

.message-end {
  text-align: center;
  text-transform: capitalize;
  padding-bottom: 50px;
  padding-top: 10px;
  color: rgba(54, 51, 51, 0.5);
}

.recipes {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

